@import './common-variables';

// lots of descendant selectors... don't follow my example kids, descendant selectors are evil

.resume {
  font-family: "Cabin", sans-serif;

  margin-top: 0px;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 0px;

  padding: $gutter;

  background-color: $background_color;
  color: $text_color;

  @media not print {
    // specifically to keep the animations contained to the "paper", overflowing outside of that would be a content/styling error
    // but still let print overflow so the test_pdf_page_count.js script can detect when the output is overflowing (spills on to and prints > 1 page)
    overflow: hidden;
  }
  & section:not(.primary-section):nth-child(odd) {
    @include slide-in-from-right;
  }
  & section:not(.primary-section):nth-child(even) {
    @include slide-in-from-left;
  }

  & section {
    margin: $gutter;
    padding: $gutter;
    border: $border solid $text_color; /* Consider having no borders? */
  }

  & h1 {
    font-size: 2.8em;

    &~h2 {
      font-size: 1.8em;
    }
  }
  & h1, & h2, & h3, & h3 {
    margin: 0px;
  }

  & p, & :not(li) > ul{
    margin: 0.25em 0em 0em 0em;
  }

  & ul, & li {
    padding-bottom: 0.27em;
  }
  & ul {
    padding-left: 1em;
    display: flex;
    flex-direction: column;
  }
  & li {
    list-style: none;

    & > p {
      margin: 0em;
    }

    & li li, & li ul {
      padding-bottom: 0em;
    }
  }
  & li:before {
    float: left;
    width: 1em;
    height: 1em;
    transform: translate(-0.05em, 0.15em);
  }
  & :not(li) > ul > li:before {
    content: url('./svgs/arrow-full.svg');
  }
  & li > ul > li:before{
    content: url('./svgs/arrow-half.svg');
  }
  & li > ul > li > ul > li:before{
    content: url('./svgs/arrow-empty.svg');
  }
}